@media screen and (min-width: 1250px) {
  .loading {
    margin-left: 15px;
  }
  .loader {
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 100vh;
    justify-content: center;
    display: flex;
    align-items: center;
    background: rgba(0, 0, 0, 0.1);
  }
  .loader h6 {
    font-family: Kanit;
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: 33px;
    color: #245462;
  }
  .empty_text {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    color: #af527a;
    font-family: Roboto;
    font-weight: normal;
  }
  .menu {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    background: #313131;
  }
  .menu-container {
    width: 1140px;
    margin: auto;
    height: 38px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .menuColumn1 {
    width: 230px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .menuColumn2 {
    width: 250px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .menuColumn1 img {
    display: none;
  }

  /* contact-link */
  .active_page {
    display: none !important;
  }
  .cart_link_contact {
    position: relative;
    padding: 5px;
  }
  .contact_icon_outline {
    position: absolute;
    color: #000;
    background-color: #ffffff;
    top: 8px;
    z-index: 10;
    font-size: 22px;
  }
  .contact_icon {
    z-index: 0;
    color: #000;
    font-size: 22px;
  }
  .cart_link_contact:hover .contact_icon_outline {
    display: none !important;
  }

  /* basket_icon */

  .cart_link_basket {
    position: relative;
    padding: 5px;
  }
  .cart_link_basket:hover .cart_icon_outline {
    display: none;
  }
  .cart_link_basket span {
    position: absolute;
    z-index: 100;
    top: 0px;
    right: 1px;
    min-width: 15px;
    min-height: 15px;
    max-height: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 4px 4px;
    padding-bottom: 0;
    padding-top: 2px;
    background-color: #e92d2d;
    color: #fff;
    font-size: 13px;
    font-family: Roboto;
    font-weight: 500;
  }
  .cart_icon_outline {
    position: absolute;
    color: #000;
    font-size: 26px;
    top: 7px;
    background-color: #ffffff;
  }
  .cart_icon {
    color: #000000;
    font-size: 26px;
    margin-top: 2px;
  }
  /* .heart_icon */
  .cart_link_heart {
    position: relative;
    padding: 5px;
  }
  .heart_icon_outline {
    color: #000000;
    font-size: 24px;
    position: absolute;
    top: 7px;
    background-color: #ffffff;
  }
  .heart_icon {
    color: #000000;
    font-size: 24px;
  }
  .cart_link_heart:hover .heart_icon_outline {
    display: none;
  }
  .homeIcon,
  .contactsIcon,
  .aboutIcon {
    display: none;
  }
  .menuColumn2 img {
    width: 29px;
    margin-right: 10px;
  }

  .menuColumn2 a,
  .menuColumn1 a {
    text-decoration: none;
    color: rgb(199, 196, 196);
    font-family: Roboto, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: left;
  }
  .menuColumn2 a:hover,
  .menuColumn1 a:hover {
    filter: opacity(1) drop-shadow(0 0 0 white);
  }
  .menu-icon-border {
    display: none;
  }
  .logo {
    width: 113px;
    height: 25px;
  }
  .logoSearch {
    position: relative;
    width: 100%;
    margin-top: 30px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logoSearchIcons {
    width: 130px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logoSearchIcons .menuColumn2 {
    display: none;
  }
  .mobile-input {
    display: none;
  }
  .inputStyle {
    outline: none;
    border: 1px solid #e6e6e6;
    height: 100%;
    width: 800px;
    padding: 10px 20px;
    padding-left: 50px;
    font-family: Roboto, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    color: #414141;
  }
  .mobile_search_compate {
    display: none;
  }
  .search_complate {
    width: 800px;
    min-height: 0px;
    display: none;
    position: absolute;
    top: 110%;
    left: 161px;
    box-shadow: 0px 2px 4px #f9f9f9 !important;
    z-index: 100;
    background-color: #ffffff;
    padding: 0;
  }
  .term {
    display: block;
  }
  .search_complate a li {
    font-family: Roboto, sans-serif;
    font-size: 15px;
    padding: 8px 25px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    color: #414141;
    list-style: none;
    cursor: pointer;
  }
  .search_complate a {
    text-decoration: none;
    width: 800px !important;
    height: 100%;
    color: #414141;
    background-color: #245462;
  }
  .search_complate a:hover li {
    background: #f9f9f9;
    color: #414141;
  }

  .inputStyle ::placeholder {
    font-family: Roboto, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: left;
    color: #414141;
  }
  .mobile_searchIcon {
    display: none;
  }
  .searchIcon {
    position: absolute;
    left: 180px;
    top: 18px;
    cursor: pointer;
  }
  /* categories */

  .categories {
    width: 100%;
    height: 70px;
    padding: 0;
    margin: 25px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    background: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.07);
  }
  .categories li {
    display: flex;
    align-items: center;
  }

  .categories a:hover {
    background: #245462;
    color: #ffffff;
  }
  .parent-category .down-icon {
    position: absolute;
    top: 1px;
    right: -17px;
    width: 11px;
    transition: transform 0.2s ease-out;
  }
  .categories .category-title:hover .parent-category .down-icon,
  .etcMenu .etc-item:hover .parent-category .down-icon {
    transform: rotateZ(-180deg);
  }
  .categories .category-title {
    height: 100%;
    position: relative;
    z-index: 1;
  }
  .categories .category,
  .stock {
    height: 100%;
    text-decoration: none;
    padding: 15px 28px;
    display: flex;
    align-items: center;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0em;
    cursor: pointer;
    color: #414141;
  }

  .categories .stock img {
    display: none;
  }
  .categories .category:hover .mobile-category-icon img {
    filter: invert(1) !important;
  }
  .etcMenu .etc-item:hover .mobile-category-icon {
    filter: invert(1) !important;
  }
  .categories .category:nth-child(n + 7) {
    display: none !important;
  }
  .categories a {
    text-decoration: none;
  }
  .categories li p {
    position: relative;
    margin: 0;
  }
  .category p {
    letter-spacing: 0.5px;
    text-align: center;
  }

  .categories .category:hover {
    background: #245462;
    color: #ffffff;
  }
  .categories .category-title:hover .child-categories {
    display: block !important;
  }
  .child-categories {
    position: absolute;
    top: 69px;
    width: 180px;
    display: none;
    transition: display 0.2s linear;
    left: 0;
    padding: 0px 25px !important;
    background: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.07);
  }
  .child-categories a {
    text-decoration: none;
  }
  .child-element {
    height: 35px !important;
    padding: 0 !important;
    display: flex;
    align-items: center;
    font-family: Roboto !important;
    font-size: 16px !important;
    font-style: normal !important;
    font-weight: 400 !important;
    letter-spacing: 0em !important;
    color: #414141 !important;
    list-style-type: none;
  }
  .child-element:hover {
    background: #ffffff !important;
    color: #245462 !important;
  }
  .stock {
    display: flex;
    justify-content: space-between;
    color: #e92d2d !important;
  }
  .vector {
    margin-right: 10px;
  }
  .stock:hover {
    color: #e92d2d !important;
    background-color: white !important;
  }
  .etc {
    height: 70px !important;
    position: relative;
    padding: 20px 26px !important;
  }

  .etcMenu {
    display: none;
    position: absolute;
    padding: 0;
    top: 70px;
    right: 0px;
    z-index: 1;
    background: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.07);
  }
  .etcMenu .etc-item {
    position: relative;
    width: 200px;
    height: 70px;
    display: flex;
    align-items: center;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
    cursor: pointer;
    color: #414141;
  }
  .etcMenu .etc-item a {
    padding: 0 26px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #414141;
  }
  .etcMenu .etc-item p {
    margin: 0;
  }

  .etc:hover .etcMenu {
    display: block;
  }
  .etc:hover {
    background-color: #ffffff !important;
    cursor: pointer;
  }
  .etcMenu .etc-item:nth-child(-n + 6) {
    display: none;
  }
  .etcMenu .etc-item a:hover {
    background: #245462;
    color: #fff;
  }
  .etcMenu .etc-item .child-etc-categories a:hover {
    color: #245462 !important;
    background: #fff !important;
  }
  .etcMenu .etc-item .child-etc-categories {
    position: absolute;
    top: 0px;
    width: 100%;
    display: none;
    right: 100%;
    z-index: 2;
    padding: 0px !important;
    background: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.07);
  }
  .child-etc-element {
    width: 100%;
    height: 35px !important;
    padding: 0 !important;
    display: flex;
    align-items: center;
    font-family: Roboto !important;
    font-size: 15px !important;
    font-style: normal !important;
    font-weight: 400 !important;
    letter-spacing: 0em !important;
    color: #414141 !important;
    list-style-type: none;
  }
  .etc-item:hover .child-etc-categories {
    display: block !important;
  }
  .menuBar {
    display: none;
  }

  /* slider */

  .sliders {
    position: relative;
    width: 1140px;
    height: 450px;
    margin-bottom: 40px;
    overflow: hidden;
  }
  .active-slide {
    position: absolute;
    z-index: 0 !important;
    width: 1140px;
    height: 450px;
    opacity: 1;
    top: 0;
    left: 0;
    transition: 0.4s ease-out;
  }
  .next-slide {
    position: absolute;
    width: 1140px;
    height: 450px;
    opacity: 0;
    transition: 0.4s ease-out;
    top: 0;
    left: 100%;
  }
  .last-slide {
    position: absolute;
    width: 1140px;
    height: 450px;
    opacity: 0;
    transition: 0.4s ease-out;
    top: 0;
    left: -100%;
  }
  .last-slide img,
  .next-slide img,
  .active-slide img {
    width: 1140px !important;
    height: 100% !important;
    object-fit: cover;
  }
  .slider-items {
    position: absolute;
    width: 300px;
    top: 30%;
    left: 12%;
  }
  .slider-items h1 {
    font-family: Playfair Display;
    font-size: 50px;
    font-style: normal;
    font-weight: 700;
    line-height: 53px;
    letter-spacing: 0.02em;
    text-align: left;
    color: #245462;
    text-transform: uppercase;
  }
  .slider-items p {
    font-family: Roboto Condensed;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: 0.02em;
    text-align: left;
  }
  .slider-items button {
    width: 163px;
    height: 45px;
    background: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.07);
    border: none;
    font-family: Roboto Condensed;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 0.04em;
    color: #245462;
    margin-top: 5px;
  }
  .slider-items button:hover {
    background: #245462;
    color: #f1f1f1;
  }
  .slider-items button a {
    text-decoration: none;
    font-family: Roboto Condensed;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 0.04em;
    color: #245462;
  }
  .arrow-back,
  .arrow-forward {
    width: 30px;
    height: 30px;
    background-color: #b8b5b5;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
  }
  .arrow-back:hover,
  .arrow-forward:hover {
    background-color: #ffffff;
  }

  .arrow-back:hover .back-icon,
  .arrow-forward:hover .forward-icon {
    color: #9d9d9d;
  }
  .arrow-back {
    position: absolute;
    left: 20px;
    top: 50%;
  }
  .arrow-forward {
    position: absolute;
    right: 20px;
    top: 50%;
  }
  .back-icon,
  .forward-icon {
    color: #fff;
  }

  /* Footer */

  .footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 10px;
    margin-top: 10px;
    width: 100%;
    height: 230px;
    background: #ffffff;
  }
  .footer-navigation-LM {
    width: 100%;
    height: 100px;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .footer-categories {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .footer-categories h6 {
    font-family: Roboto;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    letter-spacing: 0em;
    color: #000000;
  }
  .navigation-categories {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .navigation-item {
    min-width: 20%;
    height: 100%;
    margin-right: 10px;
    display: flex;
    flex-direction: column;
  }
  .navigation-item a {
    cursor: pointer;
    margin-top: 3px !important;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    margin: 0;
    color: #414141;
    text-decoration: none;
    letter-spacing: 0em;
  }

  .navigation-item a:hover {
    color: #245462 !important;
  }
  .footer-contact-item2 a:hover {
    filter: drop-shadow(0 0 0 #000);
  }
  .LM {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    width: 30%;
    height: 100%;
  }
  .LM p {
    width: 170px;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    margin: 0 !important;
    letter-spacing: 0em;
    text-align: right !important;
  }
  .footer-contact {
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .footer-contact-item1 {
    width: 50%;
    height: 100%;
    box-sizing: border-box;
  }
  .footer-contact .footer-contact-item1 .stock {
    padding: 0;
    list-style: none;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0em;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .footer-contact-item1 .stock a {
    color: #e92d2d !important;
    text-decoration: none;
  }
  .footer-contact-item2 {
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .footer-contact-item2 a {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .footer-contact-item2 a span {
    font-family: Roboto;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    color: #414141;
  }
  .footer-contact-item2 a img {
    margin-right: 8px;
    width: 16px;
  }

  /* Products */

  .stock-price {
    position: absolute;
    display: none;
    align-items: center;
    top: 18px;
  }
  .active-stock-price {
    display: flex;
  }
  .active-discounted-price {
    display: block !important;
  }
  .stock-price p {
    margin-bottom: 0 !important;
    padding: 0 !important;
    font-family: Roboto !important;
    font-size: 12px !important;
    font-style: normal !important;
    font-weight: 400 !important;
    line-height: 14px !important;
    color: #000000 !important;
  }
  .stock-price img {
    margin-right: 7px;
    width: 17px;
  }
  .price .discounted-price {
    display: none;
    font-family: Roboto;
    margin: 0 !important;
    margin-top: 2px !important;
    margin-left: 10px !important;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-decoration-line: line-through;
    color: #cccccc;
  }
  .best-sellers {
    width: 100%;
  }
  .best-sellers h5 {
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    text-transform: capitalize;
    line-height: 19px;
    letter-spacing: 0em;
  }
  .products {
    width: 100%;
    min-height: 180px;
    margin: 20px 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
  }
  .product-hover {
    position: relative;
    text-decoration: none;
    width: 260px;
    height: 350px;
    margin-right: 33px;
  }
  .product {
    position: relative;
    width: 260px;
    max-height: 310px;
    top: 0 !important;
    left: 0 !important;
    text-decoration: none;
    z-index: 0;
    overflow: hidden;
    padding: 15px;
  }
  .product-hover:nth-child(4n) {
    margin-right: 0px !important;
  }
  .product:hover {
    position: absolute;
    transition: 0.25s ease-in-out;
    max-height: 440px;
    top: 0;
    left: 0;
    z-index: 1;
    background: #ffffff;
    box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.11);
  }
  .product:hover .product-size {
    opacity: 1;
    transition: opacity 0.4s ease-in-out;
  }
  .product h6 {
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    color: #414141;
  }
  .product p {
    font-family: Roboto;
    font-size: 11px;
    font-style: normal;
    color: #414141;
    font-weight: 400;
    margin-bottom: 8px;
    line-height: 13px;
    display: flex;
    align-items: center;
  }
  .product .price {
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    display: flex;
    align-items: center;
    margin: 0 !important;
    padding: 0 !important;
  }

  .product .product-photo {
    width: 200px;
    height: 150px;
    margin: 20px auto;
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .product .product-photo img {
    width: 100%;
    height: 100%;
    object-fit: scale-down;
  }
  .product-size {
    opacity: 0;
  }
  .size-item {
    display: flex;
    align-items: center;
  }
  .product-heart-icon {
    position: absolute;
    padding: 10px;
    cursor: pointer;
    top: 5px;
    right: 5px;
  }
  .product-stock-icon {
    display: none;
    padding: 10px;
    position: absolute;
    top: 5px;
    left: 5px;
  }
  .add-to-cart:hover {
    opacity: 0.9 !important;
  }
  .add-to-cart {
    width: 100%;
    height: 40px;
    background: #245462;
    border: none;
  }
  .add-to-cart p {
    color: #ffffff;
    font-family: Roboto;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 !important;
    margin: 0 !important;
    margin-bottom: 2px !important;
  }
  .product-size h6 {
    font-family: Roboto;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    margin-top: 10px;
  }
  .size-item {
    display: flex;
    justify-content: space-between;
